.videoScreen {
    margin: 40px 0px;


    .left {
        margin-right: 2em;
        float: left;

        .video {
            zoom: 1.8
        }

        @media screen and (max-width: 992px) {
            float: none;
            margin-right: 0;
        }
    }



    .title {
        font-weight: 900;
        font-size: 36px;
    }

    .tags {
        margin: 10px 0px;
        font-size: 20px;
        color: black;
        font: normal normal normal 20px/27px Helvetica Neue;
    }

    .text {
        font-size: 24px;
        line-height: 36px;
    }

}